<template>
  <div class="main-1000">
    <div class="news">
      <div class="news-main">
        <h3>新闻详情页</h3>
        <h4>{{shijian}}</h4>
        <h2>route: {{address}}</h2>
        <p>新闻id: <span>{{newsId}}</span></p>
        <div style="text-align: center;">
          <router-link to="/news/detail/1111" tag="span">上一篇</router-link> |
          <router-link to="/news/detail/4444" tag="span">下一篇</router-link>
        </div>
        <div class="news-content">
          <div class="news-content-item">
            <h3 class="title">{{newsContent.title}}</h3>
            <p class="info"><span class="time">{{newsContent.date}}</span></p>
            <div class="article">
              <p v-for="(item, index) in newsContent.paragraphs" :key="index">{{item}}</p>
            </div>
          </div>
        </div>
      </div>
      <div class="news-adver">
        <!-- 微信 -->
        <div class="news-adver-item">
          <div class="weixin">
            <img src="http://www.cwea.org.cn/images/weixin.jpg" alt="">
            <p class="title">
              <span>扫描二维码关注</span>
              <span>《风能》官方微信公众号</span>
            </p>
          </div>
        </div>
        <!-- 企业新闻广告1 -->
        <div class="news-adver-item">
          <a href="http://www.cwea.org.cn/events_fengdianchang.html">
            <img src="http://www.cwea.org.cn/images/member-qiye-news_05.jpg" alt="">
          </a>
        </div>
        <!-- 广告位 -->
        <div class="news-adver-item">
          <a href="http://chinawindpower.org.cn" target=_blank>
            <img src="http://www.cwea.org.cn/images/member-qiye-news_06.jpg" alt="2019年北京国际风能大会暨展览会（CWP）">
          </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'NewsDetail',
  data () {
    return {
      title: 'News-Detail',
      newsContent: {
        title: 'title test',
        date: '2020-03-21',
        paragraphs: []
      },
    }
  },
  watch: {
    newsId: function () {
      // console.log('id', this.newsId)
      const id = this.newsId
      this.$axios(`api/news/${id}`).then((res) => {
        if (res) {
          this.newsContent = res.data
        }
      })
    }
  },
  computed: {
    newsId () {
      return this.$route.params.id
    },
    shijian () {
      return Date.now()
    },
    address () {
      const id = this.$route.params.id
      return '当前地址: ' + id
    }
  },
  methods: {
    getData () {
      var articleId = this.$route.params.id
      this.$axios.get(`api/news/${articleId}`).then((res) => {
        if (res) {
          this.newsContent = res.data
        }
      })
    }
  },
  created () {
    console.log('createds', this.$route.params, this.obj)
    this.getData()
  },
  beforeUpdate () {
    console.log('beforeUpdate')
    // this.getData()
  },
  updated () {
    console.log('updated')
  },
  activated () {
    console.log('activated')
  },
  deactivated () {
    console.log('deactivated')
  },
  beforeDestroy () {
    console.log('beforeDestroy')
  },
  destroyed () {
    console.log('destroyed')
  },
}
</script>

<style scoped lang="scss">
  .news h3 {
    margin: 0;
  }

  .news .info {
    color: #009e3b;
    text-align: center;
    padding: 10px 0;

    span {
      padding: 0 10px;
    }
  }

  .news .article {
    padding: 10px 0;

    p {
      text-indent: 2em;
      font-size: 12px;
      line-height: 1.5;
      padding-bottom: 10px;
    }
  }
</style>
